import "./App.css";
import React from "react";
import { Table } from "antd";
import API_URL from "./config";

function App() {
  const [dataSource, setDataSource] = React.useState([]);
  const columns = [
    {
      title: "Token ID",
      dataIndex: "token_id",
      key: "token_id",
      sorter: (a, b) => parseInt(a.token_id) - parseInt(b.token_id),
    },
    {
      title: "Ask Price",
      dataIndex: "ask_price",
      key: "ask_price",
      filters: [
        {
          text: "Exclude null",
          value: null,
        },
      ],
      onFilter: (value, record) => record.ask_price !== null,
      sorter: (a, b) =>
        parseFloat(a.ask_price === null ? 0 : a.ask_price) -
        parseFloat(b.ask_price === null ? 0 : b.ask_price),
    },
    {
      title: "Ask Sale Date",
      dataIndex: "ask_sale_date",
      key: "ask_sale_date",
      filters: [
        {
          text: "Exclude null",
          value: null,
        },
      ],
      onFilter: (value, record) => record.ask_sale_date !== null,
      render: (item) =>
        item === null
          ? null
          : new Date(item).toLocaleDateString() +
            " " +
            new Date(item).toLocaleTimeString(),
      sorter: (a, b) =>
        new Date(a.ask_sale_date).getTime() -
        new Date(b.ask_sale_date).getTime(),
    },
    {
      title: "Last Sale",
      dataIndex: "last_sale",
      key: "last_sale",
      filters: [
        {
          text: "Exclude null",
          value: null,
        },
      ],
      onFilter: (value, record) => record.last_sale !== null,
      sorter: (a, b) =>
        parseFloat(a.last_sale === null ? 0 : a.last_sale) -
        parseFloat(b.last_sale === null ? 0 : b.last_sale),
    },
    {
      title: "Last Sale Date",
      dataIndex: "last_sale_date",
      key: "last_sale_date",
      filters: [
        {
          text: "Exclude null",
          value: null,
        },
      ],
      onFilter: (value, record) => record.last_sale_date !== null,
      render: (item) =>
        item === null
          ? null
          : new Date(item).toLocaleDateString() +
            " " +
            new Date(item).toLocaleTimeString(),
      sorter: (a, b) =>
        new Date(a.last_sale_date).getTime() -
        new Date(b.last_sale_date).getTime(),
    },
    {
      title: "Opensea Link",
      dataIndex: "opensea_link",
      key: "opensea_link",
    },
  ];

  React.useEffect(() => {
    fetch(API_URL + "/data")
      .then((response) => response.json())
      .then((result) => setDataSource(result))
      .catch((error) => console.log(error));
  }, []);

  return (
    <div style={{ padding: 10 }}>
      <Table
        dataSource={dataSource}
        columns={columns}
        bordered
        summary={() => (
          <Table.Summary fixed>
            <Table.Summary.Row>
              <Table.Summary.Cell index={0} colSpan={2}>
                Total fetched tokens number
              </Table.Summary.Cell>
              <Table.Summary.Cell index={1} colSpan={4}>
                {dataSource.length}
              </Table.Summary.Cell>
            </Table.Summary.Row>
          </Table.Summary>
        )}
      />
    </div>
  );
}

export default App;
